import Link from "next/link";
import Image from "next/image";

export default function Home() {
  return (
    <div className="min-h-screen grid grid-cols-1 md:grid-cols-2 font-[Inter]">

      {/* -------- LEFT SECTION : Background Image -------- */}
      <div className="relative w-full h-64 md:h-auto">
        <Image
          src="./public/background.jpg"              // <- your image from public folder
          alt="Fashion Background"
          className="object-cover"
          fill
          priority
        />

        {/* Optional dark overlay for better text visibility */}
        <div className="absolute inset-0 bg-black/30"></div>

        {/* Optional Text on Background */}
        <div className="absolute bottom-6 left-6 text-white drop-shadow-lg">
          <h1 className="text-4xl font-extrabold">Pehnava</h1>
          <p className="text-sm text-gray-200 max-w-sm">
            Discover fashion. Inspire style.
          </p>
        </div>
      </div>

      {/* -------- RIGHT SECTION : Login / Register / About -------- */}
      <div className="flex flex-col items-center justify-center px-8 py-10 bg-gray-50">

        <h2 className="text-3xl md:text-4xl font-extrabold tracking-tight text-gray-900 mb-4">
          Welcome to <span className="text-blue-600">Pehnava</span>
        </h2>

        <p className="text-gray-600 text-center max-w-md mb-8 leading-relaxed">
          Pehnava is a peer-to-peer fashion community where users, influencers, 
          and marketing professionals share styles, opinions, outfits, and product recommendations.
        </p>

        {/* Buttons */}
        <div className="flex gap-4 mt-4">
          <Link
            href="/login"
            className="px-6 py-3 bg-blue-600 text-white text-lg font-semibold rounded-lg shadow-md hover:bg-blue-700 transition-all"
          >
            Login
          </Link>

          <Link
            href="/register"
            className="px-6 py-3 bg-gray-900 text-white text-lg font-semibold rounded-lg shadow-md hover:bg-gray-800 transition-all"
          >
            Register
          </Link>
        </div>
      </div>
    </div>
  );
}
